import Vue from 'vue'
import Router from 'vue-router'
import EmployeeTypeRegister from '../EmployeeTypeRegister'
import WorkTimeTypeRegister from '../WorkTimeTypeRegister'
// import WorkTimeTypeRegister1 from '../WorkTimeTypeRegister1'
import EnvironmentRegister from '../EnvironmentRegister'
import login from '../login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'WorkTimeTypeRegister',
      component: WorkTimeTypeRegister
    },
    {
      path: '/EmployeeTypeRegister',
      name: 'EmployeeTypeRegister',
      component: EmployeeTypeRegister
    },
    {
      path: '/WorkTimeTypeRegister',
      name: 'WorkTimeTypeRegister',
      component: WorkTimeTypeRegister
    },
    // {
    //   path: '/WorkTimeTypeRegister1',
    //   name: 'WorkTimeTypeRegister1',
    //   component: WorkTimeTypeRegister1
    // },
    {
      path: '/EnvironmentRegister',
      name: 'EnvironmentRegister',
      component: EnvironmentRegister
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }
  ]
})



